import React from "react";
import "../styles/main.scss";

const Button = ({ color, size, content }) => {
	let styles = {
		background: color,
		color: color === "black" ? "white" : "black",
	};
	let btn = (
		<div
			className={`landing-btn ${size === "lg" ? "landing-btn-xl" : null}`}
			style={styles}
		>
			{content}
		</div>
	);
	if (size === "lg") {
		styles.background = color;
		styles.padding = "1rem 1.5rem";
		styles.color = color === "black" ? "white" : "black";
		styles.textTransform = "uppercase";
	}
	return btn;
};

export default Button;
