import React from "react";
import s1 from "../../assets/s1.svg";
import s2 from "../../assets/s2.svg";
import s3 from "../../assets/s3.svg";
import {
	faArrowDown,
	faArrowUp,
	faDotCircle,
	faPaperPlane,
	faPhoneVolume,
	faStar,
	faStarHalf,
	faThumbsUp,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

let sliderData = [
	{
		image: s1,
		calls: 1200,
		emails: 600,
		rating: 4.5,
		name: "Kartik",
		title: "The Learner",
	},
	{
		image: s2,
		calls: 800,
		emails: 300,
		rating: 4,
		name: "Shyam",
		title: "Tehcnical Notorious",
	},
	{
		image: s3,
		calls: 1500,
		emails: 1000,
		rating: 5,
		name: "Ram",
		title: "Perfect Personality",
	},
];

const getStars = (stars) => {
	let starsList = [];
	for (let i = 1; i <= stars; i++) {
		starsList.push(
			<FontAwesomeIcon key={i} icon={faStar} color="gold" size="xs" />
		);
	}
	if (!Number.isInteger(stars)) {
		starsList.push(
			<FontAwesomeIcon
				key={"half"}
				icon={faStarHalf}
				color="gold"
				size="xs"
			/>
		);
	}
	return starsList;
};

const LandingRight = () => {
	return (
		<div className="landing-right">
			<div className="slider-tags">
				<div className="slider-tag-bar">
					<div className="bar bar1">
						<FontAwesomeIcon icon={faPhoneVolume} size="lg" />
					</div>
					<div className="bar-text">Calls Made</div>
					<div>
						{new Intl.NumberFormat("en-IN", {
							maximumSignificantDigits: 3,
						}).format(sliderData[0].calls)}
					</div>
				</div>
				<div className="slider-tag-bar">
					<div className="slider-tag-bar">
						<div className="bar bar2">
							<FontAwesomeIcon icon={faPaperPlane} size="lg" />
						</div>
						<div className="bar-text">Email Sent</div>
						<div>
							{new Intl.NumberFormat("en-IN", {
								maximumSignificantDigits: 3,
							}).format(sliderData[0].emails)}
						</div>
					</div>
				</div>
				<div className="slider-tag-bar">
					<div className="bar bar3">
						<FontAwesomeIcon icon={faThumbsUp} size="lg" />
					</div>
					<div className="bar-text">Reliability</div>
					<div className="stars">
						{getStars(sliderData[0].rating)}
					</div>
				</div>
			</div>
			<div className="landing-slider">
				<img
					className="landing-slider-image"
					src={sliderData[0].image}
					alt="s1"
				/>
			</div>
			<div className="slider-controller">
				<ul>
					{sliderData.map((item, index) => {
						return (
							<li key={index}>
								<FontAwesomeIcon icon={faDotCircle} />
							</li>
						);
					})}
				</ul>
				<div className="slider-desc">
					{sliderData[0].name}
					<p>{sliderData[0].title}</p>
				</div>
				<div className="arrow slider-arrow-up">
					<FontAwesomeIcon icon={faArrowUp} />
				</div>
				<div className="arrow slider-arrow-down">
					<FontAwesomeIcon icon={faArrowDown} />
				</div>
			</div>
		</div>
	);
};

export default LandingRight;
